import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {MotoModel} from '../models/moto-model';

@Injectable({
  providedIn: 'root'
})
export class MotoService {

  private url = 'http://localhost:8080/moto';

  constructor(private http: HttpClient) { }

  /**
   * Method to get One Moto from database
   * @param id Id of moto
   */
  getOneMoto(id: string): Observable<MotoModel> {
    return this.http.get<MotoModel>(this.url + '/get/' + id);
  }

  /**
   * Method to get the count of all Moto
   */
  countAllMoto(): Observable<number> {
    return this.http.get<number>(this.url + '/count');
  }
}
