import {Component, OnInit} from '@angular/core';
import {MarqueModel} from '../../models/marque-model';
import {MarqueService} from '../../services/marque.service';
import {MotoService} from '../../services/moto.service';
import {MotoModel} from '../../models/moto-model';
import {take} from 'rxjs/operators';

@Component({
  selector: 'app-accueil',
  templateUrl: './accueil.component.html',
  styleUrls: ['./accueil.component.sass']
})
export class AccueilComponent implements OnInit {

  marque: MarqueModel;
  nbMarque: number;
  moto: MotoModel;
  nbMoto: number;

  constructor(private serviceMarque: MarqueService, private serviceMoto: MotoService) {
  }

  ngOnInit(): void {
    this.serviceMoto.getOneMoto('1').subscribe(
      (moto: MotoModel) => this.moto = { ...moto}
    );
    this.serviceMoto.countAllMoto().subscribe(
      res => {
        this.nbMoto = res;
      }
    );
    this.serviceMarque.getOneMarque('1').pipe(take(1)).subscribe(
      (marque: MarqueModel) => this.marque = { ...marque}
    );
    this.serviceMarque.countAllMarque().subscribe(
      res => {
        this.nbMarque = res;
      }
    );
  }

}
